@import 'https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap';

/*Nullstyle*/

* {
    padding: 0;
    margin: 0;
    border: 0;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

:focus,
:active {
    outline: none;
}

a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}

button {
    cursor: pointer;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

a,
a:visited {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

ul li {
    list-style: none;
}

img {
    vertical-align: middle;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: inherit;
}


/* ====================================================== */

html {
    background-color: black;
}

body {
    font-family: "Roboto", sans-serif;
    padding: 0 20px;
}

.hide {
    display: none;
}

.fadeIn {
    animation: fade 0.7s ease;
}

@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

header {
    display: flex;
    align-items: center;
    height: 40px;
    width: 100%;
    margin: 0 auto;
}

main {
    margin: 0 auto;
}

.statistics {
    background: linear-gradient(rgb(149, 66, 66), #fb5e5e);
    width: 100%;
}

.last__report {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    width: 80%;
    color: #fff;
    padding: 20px;
}

.country {
    height: 80px;
    cursor: pointer;
    max-width: 300px;
}

.name {
    font-size: 3em;
    color: hsl(288, 97%, 71%);
}

.find__country {
    color: hsl(288, 97%, 71%);
}

.pick__country {
    position: relative;
    margin-top: 50px;
}

.search {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    z-index: 10;
    width: 450px;
    height: 40px;
    background-color: indianred;
}

.total__cases {
    color: hsl(288, 97%, 71%);
    height: 80px;
    font-weight: 900;
    margin-bottom: 30px;
}

.recovered {
    color: hsl(106, 96%, 35%);
    margin-bottom: 30px;
}

.deaths {
    color: black;
    margin-bottom: 30px;
}

.title {
    font-size: 1.7em;
    margin-bottom: 30px;
}

.value {
    font-size: 2.5em;
    font-weight: 700;
}

.new-value {
    font-size: 1.5em;
}

.recovered {
    height: 80px;
}

.deaths {
    height: 80px;
}

#chart {
    width: 80vw;
    max-height: 300px;
    min-height: 450px;
    margin: 0 auto;
    padding: 30px 0;
}

.search input {
    width: 300px;
    height: 30px;
    background-color: transparent;
    color: #ffffff;
    padding: 10px;
}

.search input::placeholder {
    color: #fff;
    font-size: 1.2em;
}

.search img {
    width: 30px;
    cursor: pointer;
    padding: 0 10px 0 0;
}

.country-list {
    position: absolute;
    display: flex;
    margin-top: 40px;
    width: 450px;
    height: 350px;
    background-color: rgba(148, 53, 36, 0.924);
    overflow-y: scroll;
}

.country-list ul li:hover {
    box-shadow: 0 0 0 1px black;
    transition: 0.6s;
}

.country-list ul {
    margin-top: 10px;
    width: 100%;
}

.country-list li {
    cursor: pointer;
    padding: 10px 0;
    margin-left: 5px;
}

.country-list::-webkit-scrollbar {
    background-color: rgba(168, 126, 118, 0.37);
}

.country-list::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.185);
}

@media (max-width: 620px) {
    .last__report {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
}